// ========== MODAL ==========

.modal {
  z-index: 999;
  position: relative;

  margin: auto;
  border-radius: 4px;

  @include mobile-only {
    width: 320px;
    padding: 20px;
  }

  @include tablet-only {
    width: 420px;
    padding: 40px;
  }

  @include desktop-only {
    width: 520px;
    padding: 60px;
  }

  background-color: var(--primary-bg-color);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);

  transition: transform 500ms var(--timing-function) 250ms;

  .backdrop.is-hidden & {
    transform: scale(0) rotateX(-90deg);
    transition: transform 500ms var(--timing-function);
  }
}

.modal-title {
  line-height: 1.5;
  display: flex;
  justify-content: space-around;
  align-items: center;

  text-align: center;
  text-transform: uppercase;
  color: var(--title-text-color);

  @include mobile-only {
    margin-bottom: 20px;

    font-size: 14px;
  }

  @include tablet-only {
    margin-bottom: 30px;

    font-size: 18px;
  }

  @include desktop-only {
    margin-bottom: 40px;

    font-size: 22px;
  }
}

.modal-title-icon {
  fill-opacity: 0.8;

  @include tablet-only {
    width: 36px;
    height: 44px;
  }

  @include desktop-only {
    width: 50px;
    height: 58px;
  }
}

