// ========== MODAL FORM ==========

.modal-form-field {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @include tablet-only {
    &:nth-child(3) {
      margin-bottom: 40px;
    }
  }

  @include desktop-only {
    &:nth-child(3) {
      margin-bottom: 60px;
    }
  }
  // &:not(:nth-child(3)) {
  //   margin-bottom: 10px;
  // }
}

.modal-form-label {
  padding-bottom: 4px;
  padding-left: 12px;

  font-size: 14px;
  line-height: 1.16;
  letter-spacing: 0.02em;

  color: var(--primary-text-color);

  @include tablet-only {
    font-size: 15px;
  }

  @include desktop-only {
    font-size: 16px;
  }
}

.modal-form-input {
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 42px;
  outline: none;
  border: 1px solid rgba(33, 33, 33, 0.4);
  border-radius: 4px;

  font-size: 14px;

  transition: border-color 250ms var(--timing-function);

  @include mobile-only {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &:focus {
    border-color: var(--accent-color);
  }
}

.modal-input-icon {
  position: absolute;
  left: 12px;
  bottom: 11px;
  fill: currentColor;

  transition: color 250ms var(--timing-function);

  .modal-form-input:hover + &,
  .modal-form-input:focus + & {
    color: var(--accent-color);
  }
}

// ========== MODAL BUTTONS ==========

.button-modal {
  display: block;
  margin: 0 auto;

  @include mobile-only {
    width: 200px;
  }

  @include tablet-only {
    width: 220px;
  }

  @include desktop-only {
    width: 260px;
  }
}

.close-button {
  position: absolute;
  top: -8px;
  right: -8px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  cursor: pointer;

  background-color: var(--primary-bg-color);
  fill: currentColor;

  transition: color 250ms var(--timing-function),
    border-color 250ms var(--timing-function),
    transform 250ms var(--timing-function);

  &:hover,
  &:focus {
    color: var(--accent-color);
    border-color: var(--accent-color);

    transform: scale(1.1);
  }
}
